import React, { Fragment } from 'react'
import styled from '@emotion/styled'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import Layout from './../components/Layout'
import Promo from './../components/Promo'
import MenuGames from './../components/MenuGames'

import NotificationsForm from '../components/subscribe/NotificationsForm'
import Slider from 'react-slick'
// import Carousel from './../components/styledcarousel'

import HomeSections from '../components/HomeSections'
import SEO from '../components/seo'

const IndexPage = ({ data }) => {
  //console.log('results')
  //console.log(data.edges[0].node.acf.jefftestcarousel)
  const filteredResults = data.edges[0].node.acf.jefftestcarousel.filter(
    node => node && node.jimage2
  )

  //console.log(filteredResults)

  const SampleNextArrow = props => {
    const { className, style, onClick } = props
    return (
      <div
        className={className}
        style={{ ...style, display: 'block', background: 'green' }}
        onClick={onClick}
      />
    )
  }
  const SamplePrevArrow = props => {
    const { className, style, onClick } = props
    return (
      <div
        className={className}
        style={{ ...style, display: 'block', background: 'green' }}
        onClick={onClick}
      />
    )
  }

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
  }

  return (
    <Fragment>
      <Layout>
        <SEO
          title={data.edges[0].node.yoast_meta.yoast_wpseo_title}
          keywords={[`gatsby`, `application`, `react`]}
        />
        <header>
          <div id="hero-games">
            <Slider {...settings}>
              {filteredResults.map((carouselImage,index) => {
                return (
                  <Img
                    key={index}
                    fluid={
                      carouselImage.jimage2.localFile.childImageSharp.fluid
                    }
                  />
                )
              })}
            </Slider>
          </div>
        </header>
        <Promo />
        <MenuGames />
        <HomeSections />
        <NotificationsForm />
      </Layout>
    </Fragment>
  )
}

export default () => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressPage(
          filter: { template: { eq: "php/page-real-cash-home.php" } }
        ) {
          totalCount
          edges {
            node {
              id
              slug
              type
              path
              acf {
                jefftestcarousel {
                  jimage2 {
                    id
                    localFile {
                      childImageSharp {
                        fluid (maxWidth:3000){
                          ...GatsbyImageSharpFluid_withWebp
                        }
                      }
                    }
                  }
                }
              }
              yoast_meta {
                yoast_wpseo_title
                yoast_wpseo_metadesc
                yoast_wpseo_canonical
                yoast_wpseo_facebook_title
                yoast_wpseo_facebook_description
                yoast_wpseo_facebook_type
                yoast_wpseo_facebook_image
                yoast_wpseo_twitter_title
                yoast_wpseo_twitter_description
                yoast_wpseo_twitter_image
                yoast_wpseo_social_url
                yoast_wpseo_company_or_person
                yoast_wpseo_person_name
                yoast_wpseo_company_name
                yoast_wpseo_company_logo
                yoast_wpseo_website_name
              }
            }
          }
        }
      }
    `}
    render={data => <IndexPage data={data.allWordpressPage} />}
  />
)
