import React, { Fragment } from 'react'
import Loadable from 'react-loadable'
import { StaticQuery, graphql } from 'gatsby'
import SectionGameGalleryItem from './SectionGameGalleryItem'

const JackpotLoadable = Loadable({
  loader: () => import('./JackpotCarousel'),
  loading: () => <div>Loading...</div>,
})

const SectionGameGalleryWrapper = ({ data }) => {
  //console.log(data)
  return (
    <Fragment>
      <div className="games games-jackpot">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <JackpotLoadable />
            </div>
          </div>
        </div>
      </div>
      <div className="games">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <div className="row">
                <SectionGameGalleryItem wordpressIds={data} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  )
}

export default () => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressWpHomeSections(
          filter: {
            categories: { elemMatch: { slug: { eq: "section-game-gallery" } } }
          }
        ) {
          edges {
            node {
              acf {
                games {
                  wordpress_id
                }
              }
            }
          }
        }
      }
    `}
    render={data => (
      <SectionGameGalleryWrapper
        data={data.allWordpressWpHomeSections.edges[0].node.acf.games}
      />
    )}
  />
)
