import React from 'react'
import { Link, StaticQuery, graphql } from 'gatsby'
import logo from '../img/logo.svg'
import '../scss/chumba.scss'

const MenuGames = () => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressWpApiMenusMenusItems(
          filter: { slug: { eq: "footer-menu-1" } }
        ) {
          edges {
            node {
              name
              count
              items {
                order
                title
                url
                object_slug
                wordpress_children {
                  wordpress_id
                  title
                  url
                  object_slug
                }
              }
            }
          }
        }
      }
    `}
    render={data => (
      <div className="game-menu d-xs-block">
        <div className="text-center">
          {data.allWordpressWpApiMenusMenusItems.edges[0].node.items.map(
            item => (
              <div className="game-menu-item" key={item.order}>
                <Link
                  className="nav-link py-3 px-0 px-lg-3 rounded "
                  to={item.url.replace('http://localhost:8000', '')}
                  key={item.object_slug}
                >
                  {item.title}
                </Link>
              </div>
            )
          )}
        </div>
      </div>
    )}
  />
)

export default MenuGames
